import React from 'react';
import { User } from '../../types/User';

type Props = {
  user: User;
};

export const UserInfo: React.FC<Props> = ({ user }) => {
  const { name, email } = user;

  return (
    <a
      className="UserInfo card-link text-white fst-italic text-decoration-none"
      href={`mailto:${email}`}
    >
      {name}
    </a>
  );
};
